.container-n1 {
    position: relative;
  }
  
  .content-n1 h1 {
    font-size: 120px;
    color: #fff;
    font-weight: 600;
    transition: 0.5s;
    text-align: center;
  }
  
  .content-n1 h1:hover {
    -webkit-text-stroke: 2px #fff;
    color: transparent;
  }
  
  #v1 {
    color: #000;
    font-weight: bold;
    text-align: center;
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0.8);
    border: 3px rgba(255, 255, 255, 0.8);
    border-radius: 6px;
    margin: 5%;
    height: 100%;
    width: auto;
  }
  
