  #helper {
    margin: auto;
    width: 45%;
  }
  
  @media screen and (max-width: 600px) {
    .content-n1 h1 {
      font-size: 46px;
    }
  
    #helper {
      margin: auto;
      width: 95%;
    }
  }
  
  @media screen and (min-width: 600px) {
    .content-n1 h1 {
      font-size: 48px;
    }
  
    #helper {
      margin: auto;
      width: 80%;
    }
  }
  
  @media screen and (min-width: 900px) {
    .content-n1 h1 {
      font-size: 72px;
    }
  
    #helper {
      margin: auto;
      width: 65%;
    }
  }
  
